import React from 'react';

import { Switch, Route } from 'react-router-dom';
import Story from '../components/Story';
import Reels from '../components/Reels';
import Post from '../components/Post';
import PostDown from '../components/PostDown';
import { Feed } from '../components/Feed';
import FeedDownload from '../components/feedDownload';
import Error from '../components/Error';
import StoryDownload from '../components/StoryDownload';
import '../App.css';
import '../components/Styles/commonStyles.css';
import Video from '../components/Video';
import { Home } from '../components/Home';

export const INICIO = '/ferramentas/baixar-foto-perfil-instagram';
export const STORY = '/ferramentas/baixar-story-instagram';
export const REEL = '/ferramentas/baixar-reels-instagram';
export const STORYDOWNLOAD = '/ferramentas/storydownload';
export const POST = '/ferramentas/baixar-foto-instagram';
export const POSTDOWNLOAD = '/ferramentas/postdownload';
export const FEEDDOWNLOAD = '/ferramentas/feedDownload';
export const VIDEO = '/ferramentas/baixar-video-instagram';

export function Routes() {
  return (
    <>
      <Route exact path={`${INICIO}/`} component={Feed} />
      <Route exact path={`/ferramentas/`} component={Home} />
      <Route exact path={`${STORY}/`} component={Story} />
      <Route exact path={`${STORYDOWNLOAD}/`} component={StoryDownload} />
      <Route exact path={`${VIDEO}/`} component={Video} />
      <Route exact path={`${REEL}/`} component={Reels} />
      <Route exact path={`${POST}/`} component={Post} />
      <Route exact path={`${POSTDOWNLOAD}/`} component={PostDown} />
      <Route exact path={`${FEEDDOWNLOAD}/`} component={FeedDownload} />
      <Route component={Error} />
    </>
  );
}
