import Heart from '../../Media/heart.svg';
import Plays from '../../Media/plays.svg';

const LoadedReels = (props) => {
  let responseObj = props.responseObj;
  let reelId = props.reelId;
  let downloadFileName = 'reel.mp4';

  console.log(responseObj);

  // If no reel found
  if (responseObj.message) {
    return (
      <div className="error-page">
        <h1>No reel found !</h1>
        <p>Make sure you've entered proper link.</p>
      </div>
    );
  }
  responseObj = responseObj.items[0];
  const handleDownload = async (e, username, index) => {
    console.log('eeeee', e);
    const response = await fetch(
      `https://instagram-bulk-profile-scrapper.p.rapidapi.com/clients/api/ig/media_by_id?shortcode=${reelId}&response_type=reels&corsEnabled=false`,
      {
        method: 'GET',
        headers: {
          'x-rapidapi-key': `6f4a2f23f8msh32debadf0dc7a7cp141a93jsnc4ba8ede0dc6`,
          'x-rapidapi-host': 'instagram-bulk-profile-scrapper.p.rapidapi.com',
        },
      },
    );
    const data = await response.json();
    console.log('dataaaaaaa', data);
    console.log(
      'responseObj.story.data',
      data[0].items[0].image_versions2.candidates[0].url,
    );
    window.open(data[0].items[0].video_versions[0].url, '_blank');
  };

  function convertToInternationalCurrencySystem(labelValue) {
    // Nine Zeroes for Billions
    return Math.abs(Number(labelValue)) >= 1.0e9
      ? (Math.abs(Number(labelValue)) / 1.0e9).toFixed(2) + 'B'
      : // Six Zeroes for Millions
      Math.abs(Number(labelValue)) >= 1.0e6
      ? (Math.abs(Number(labelValue)) / 1.0e6).toFixed(2) + 'M'
      : // Three Zeroes for Thousands
      Math.abs(Number(labelValue)) >= 1.0e3
      ? (Math.abs(Number(labelValue)) / 1.0e3).toFixed(2) + 'K'
      : Math.abs(Number(labelValue));
  }
  return (
    <>
      <header className="reel-header">
        <div className="reel-creator">
          <img
            src={responseObj.user.profile_pic_url}
            className="profile-picture"
            alt="profile"
          />
          <div className="creator-info">
            <h3>{responseObj.user.username}</h3>
            <p>{responseObj.user.full_name}</p>
          </div>
        </div>
      </header>
      <video controls className="reel-video">
        <source
          src={responseObj.video_versions[0].url}
          type="video/webm"
          download={downloadFileName}
        ></source>
      </video>
      <div className="likes">
        <div className="plays">
          <a
            href={`https://www.impulsionegram.com.br/comprar-comentarios-instagram/?utm_source=ferramenta&utm_medium=seo&utm_campaign=likes-ferramentas`}
            target="_blank"
            className="flex"
          >
            <img src={Plays} className="nav-icons" alt="search Story" />
            <p>
              {convertToInternationalCurrencySystem(responseObj.play_count)}{' '}
              comentrios
            </p>
          </a>
        </div>
        <br />
        <div className="like-count">
          <a
            href={`https://www.impulsionegram.com.br/comprar-curtidas-instagram/?utm_source=ferramenta&utm_medium=seo&utm_campaign=likes-ferramentas`}
            target="_blank"
          >
            <img src={Heart} className="nav-icons" alt="search Story" />
            <p>
              {convertToInternationalCurrencySystem(responseObj.like_count)}{' '}
              likes
            </p>
          </a>
        </div>
      </div>

      {/* <a className = "buttonDownload" target="_blank" download = "yasin.mp4" href={responseObj.video_versions[0].url} >Download !!!</a> */}
      {/* <p className="reel-caption">{responseObj.caption.text}</p> */}
      <div
        onClick={(e) => handleDownload(e, responseObj.username)}
        className="buttonDownload"
      >
        <h4>Download</h4>
      </div>
      {/* <a className="buttonDownload" target="_blank" download="yasin.mp4" href={responseObj.video_versions[0].url} >Download</a> */}
      {/* <p className="reel-caption">{responseObj.caption.text}</p> */}

      {(() => {
        if (responseObj.caption === 'NULL') {
          return <div>No Captions</div>;
        } else if (responseObj.caption) {
          return <p className="post-caption">{responseObj.caption.text}</p>;
        }
      })()}
    </>
  );
};

export default LoadedReels;
